.videosContainer{
    position: relative;
    top: 3em;
    height: max-content;
    display: flex;
    flex-wrap: wrap;
    margin: 0.5em;

    .thumbnailCard{
        width: 22.8%;
        height: 18em;
        margin: 1em;
        color: white;

        .thumbnailCardLink{
            text-decoration: none;
        
            .thumbnailAndDuration{
                position: relative;
                text-align: center;

                .thumbnailImage{
                    border-radius: 13px;
                    width: 100%;
                }
                .duration{
                    background-color: rgba(0, 0, 0, 0.7);
                    padding: 0.1em 0.4em;
                    color: white;
                    position: absolute;
                    bottom: 0.75em;
                    right: 0.45em;
                    border-radius: 5px;
                    font-size: 0.8em;
                }
            }

            .channelIconAndTitle{
                display: flex;
                margin-top: 0.4em;

                .channelIcon{

                }

                .titleAndChannelName{
                    color: white;

                    .title{
                        font-weight: normal;
                    }
                    
                    .smallText{
                        font-weight: normal;
                        color: #989898;
                        font-size: small;
                    }

                    .channelName{
                    }
                    
                    .viewsAndDate{
                    }
                }
            }
        }
    }
}