.CategoriesBar{
    position: fixed;
    display: flex;
    width: 96.2%;
    padding: 0em 1em;
    background-color: #0f0f0f;
    
    z-index: 998;
    overflow-y: hidden;
    list-style-type: none;
    -ms-overflow-style: none;
    scrollbar-width: none;

    .active {
        color:#000000;
        background-color: rgb(218, 218, 218);
        
        &:hover{
            color:#444444;
            background-color: rgb(218, 218, 218);
        }
    }

    li{
        margin: 1vh;
        overflow: visible;
        padding: 0.5rem ;
        border-radius: 5px;
        white-space: nowrap;
        color: whitesmoke;
        background-color: #444444; 
        cursor: pointer;
        
        &:hover{
            transition: 200ms;
            background-color: #565656; 
        }
    }
}

.CategoriesBar::-webkit-scrollbar {
    display: none;
}
