.logInBtnDiv{
    display: flex;  
    align-items: center;
    margin-right: 0.6em;

    button{
        background-color:#2c2c2c;
        color: white;
        border-radius: 0 30px 30px 0;
        padding: 0.5em 1em;
        border: none;
        font-size: 0.9em;
        font-weight: bold;

        &:hover{
            transition: 300ms;
            background-color: #3f3f3f;
            cursor: pointer;
        }
    }
}